import Vue from "vue";
import VueRouter from "vue-router";

import Main from '@/views/Main.vue'
import Login from '@/views/Login.vue'

Vue.use(VueRouter);

const routes = [{
  path: '/',
  redirect: '/',
},
{
  path: '/main',
  name: 'Main',
  component: Main,
},
{
  path: '/login',
  name: 'Login',
  component: Login
},
];
const router = new VueRouter({
  mode: "history",
  routes,
});

export default router;